<template>
  <div class="gt-loading-spinner-panel" :style="`height: ${_height}`">
    <fa-icon :icon="icon" :class="[`fa-fw fa-${iconSize} text-success`, { 'fa-spin': !!spin }]" />
  </div>
</template>

<script setup>
  import AllowedIconSizes from "~/constants/AllowedIconSizes";

  const props = defineProps({
    height: {
      type: [Number, String],
      required: false,
      default() {
        return 100;
      }
    },
    icon: {
      type: String,
      required: false,
      default() {
        return "spinner";
      }
    },
    iconSize: {
      type: String,
      required: false,
      default() {
        return "2x";
      },
      validator(iconSize) {
        return AllowedIconSizes.includes(iconSize);
      }
    },
    spin: {
      type: Boolean,
      required: false,
      default() {
        return true;
      }
    }
  });

  const _height = computed(() => (!isNaN(props.height) ? props.height + "px" : props.height));
</script>

<style lang="scss">
  .gt-loading-spinner-panel {
    min-height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
</style>
