<script>
  import { BPlaceholder } from "bootstrap-vue-next";
  import LinksPanel from "./LinksPanel";
  import LoadingSpinnerPanel from "./LoadingSpinnerPanel.vue";
  import { h } from "vue";

  export default {
    props: {
      busy: Boolean,
      description: {
        type: String,
        required: false,
        default() {
          return `For more information, please see the following links with further information relevant to this article.`;
        }
      },
      links: {
        type: Array,
        required: true
      },
      title: {
        type: String,
        required: false,
        default() {
          return `Related Links`;
        }
      }
    },
    setup(props) {
      // if (props.busy) {
      //   content.push(h("h3", [h(BPlaceholder, { variant: "success", size: "lg" })]));

      // } else {
      //   content.push(h("h3", [h("a", { href: "#related-links" }, props.title)]));
      //   // content.push(h("p", () => props.description));
      // }

      //

      return () => {
        const content = [];

        content.push(
          h("h3", [
            props.busy
              ? h(BPlaceholder, {
                  animation: "glow",
                  variant: "success",
                  size: "lg"
                })
              : h("a", { href: "#related-links" }, props.title)
          ])
        );

        if (props.busy) {
          for (let i = 0; i < 3; i++) {
            content.push(
              h(BPlaceholder, {
                animation: "glow",
                key: `related-articles-text-placeholder-${i}`
              })
            );
          }
        } else {
          content.push(h("p", props.description));

          content.push(h(LinksPanel, { links: props.links }));
        }

        return h("div", { id: "related-links" }, content);
      };
    }
  };
</script>
